<template>
  <modal>
    <div slot="header">
      <h3></h3>
    </div>

    <div slot="body">
      {{ event.summary }}
      
    </div>

    <div slot="footer">
      <button @click="deleteEvent"></button>
      <button @click="$emit('close')"></button>
    </div>
  </modal>
</template>

<script>
import Modal from "./Modal.vue";

export default {
  components: {
    Modal
  },

  props: {
    event: Object,
  },

  data() {
    return {

    };
  },
  
  methods: {
    deleteEvent() {
      axios
        .delete(`/api/calendar/event/${this.event.id}`)
        .then(res => {
          console.log("deleted complete");
          this.$emit("update");
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

